import React from 'react'
import Header from '../header/header'
import Footer from '../footer/footer'
import { Main } from './styles'
import { Outlet } from 'react-router-dom'

//    
function PageWrapper({ ...prop }) {
	return (
		<React.Fragment>
			<Header />
			<Main>
				<Outlet />
			</Main>
			<Footer />
		</React.Fragment>
	)
}

export default PageWrapper
